import { useSignUpMutation } from "redux/authAPI";
import {useState, useDispatch} from 'react';
import { registration } from "redux/auth";
import { Button, TextField } from "@mui/material";
import { RegisterForm, Input } from "./styled/RegistrationForm.styled";

const RegistrationForm = () => {

    // const dispatch = useDispatch();
const [addUser] = useSignUpMutation();

const [userName, setUserName] = useState('');
const [email, setEmail] = useState('');
const [password, setPassword] = useState('');
const [repeatPassword, setRepeatPassword] = useState('');

    const reset = () => {
        setUserName('');
        setEmail('');
        setPassword('');
}    
    
const handleInput = (e) => {
    const {name, value} = e.target;

    switch (name) {
        case 'name':
            setUserName(value);
            break;

        case 'email':
            setEmail(value);
            break;

        case 'password':
            setPassword(value);
            break;

        case 'repeatPassword':
            setRepeatPassword(value);
            break;

        default:
            return;
    }
}

const createUser = async (e) => {
    e.preventDefault();
    reset();
    if (repeatPassword === password) {
        // dispatch(registration({name: userName, email, password}));
        try {
            const newUser = await addUser({name: userName, email, password});
            console.log('User successfully created')
            return newUser;
        }
        catch(error) {
            console.log(error);
        }
    }
    // alert('your password not equal')
}

    return (

        <RegisterForm onSubmit={createUser}>
            <Input id="standard-basic" label="Name" variant="standard" type="text" name="name" onChange={handleInput}/>
            <Input id="standard-basic" label="Email" variant="standard" type="email" name="email" onChange={handleInput}/>
            <Input id="standard-basic" label="Password" variant="standard" input type="password" name="password" onChange={handleInput}/>
            {/* <label>Input your password again
            <input type="password" name="repeatPassword"onChange={handleInput}/>
            </label> */}
            <Button variant="contained" color="success" type="submit">Register</Button>
        </RegisterForm>
    )
}

export default RegistrationForm;